:root {
  --transition-speed: 10s;
}

.header {
  display: flex;
  justify-content: space-evenly;
  padding: 5px;
  background-color: #f1f1f1;
}

.mainContent {
  margin: 10px;
  padding: 5px;
}

.homeLink {
  text-decoration: none;
  color: salmon;
}

h1 {
  color: salmon;
  font-size: 7vh;
}

h2 {
  color: black;
  font-size: 4vh;
}

.App {
  height: 100vh;
  text-align: center;

}

.logo {
  size: auto;
  margin: 10px;
}

/* Style the navigation menu */
.topnav {
  display: flex;
  align-items: center;
  padding: 5px;
  overflow: hidden;
  background-image: url(./assets/pexels-simone-defendi-12762122.jpg);
  background-size: cover;

}

/* Hide the links inside the navigation menu (except for logo/home) */
.topnav #myLinks {
  display: none;
}

/* Style navigation menu links */
.topnav a {
  color: white;
  text-decoration: none;
  font-size: 30px;
  display: block;
  text-align: -webkit-center;
  padding: 10px 0 10px 0;
  text-shadow: 1px 1px 1px black;
}

.topnav a:active {
  text-shadow: 5px 5px 5px black;
}

/* Style the hamburger menu */
.topnav a.icon {
  display: block;
  right: 0;
  top: 0;
  padding: 10px;
}

/* Add a grey background color on mouse-over */
*/ .topnav a:hover {
  background-color: #ddd;
  color: black;
}





@media screen and (max-width: 900px) {
  .header {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  .header h1 {
    color: black;
    font-size: 4vh;
  }

  .logo {
    display: flex;
    justify-content: center;
    width: 30%;
    border-radius: 10px;
    box-shadow: 0px 0px 5px 1px;
  }

}